.tools {
  grid-area: tools;
}

.tools__canvas-size {
  margin: 5px;
}

.tools__canvas-size button {
  padding: 5px;
  background: #4c4c4c;
  border: none;
  margin: 2px;
  color: #ffffff;
}

.tools__basic {
  display: flex;
  flex-wrap: wrap;
  padding-left: 10px;
}

.tools__button {
  outline: none;
  background: #4c4c4c;
  border: 3px solid #4c4c4c;
  padding: 10px;
  height: 55px;
  width: 55px;
  margin: 3px;
  color: white;
}

.tools__color {
  position: relative;
  margin: 10px 0 5px 22px;
}

.tools__firstColor, .tools__secondColor {
  height: 50px;
  width: 50px;
  cursor: cell;
  background: #868686;
  border: none;
  border-radius: 3px;
}

.tools__firstColor {
  z-index: 10;
  position: absolute;
  top: 0;
  left: 15px;
}

.tools__secondColor {
  position: absolute;
  top: 25px;
  left: 41px;
}

.tools__highlight {
  border-color: #ffd700;
  z-index: 10;
}
