.frames {
  grid-area: frames;
}

.frames__buttons {
  display: flex;
  margin-left: 23px;
}

.frames__add-frame, .frames__revers-frame{
  outline: none;
  padding: 10px;
  border: 3px solid #808080;
  border-radius: 3px;
  background: none;
  color: #808080;
  font-weight: bold;
  width: 62px;
  height: 62px;
  margin: 5px;
}

.frames__revers-frame {
  outline: none;
  padding: 10px;
  border: 3px solid #808080;
  border-radius: 3px;
  background: none;
  color: #808080;
  font-weight: bold;
  width: 62px;
  height: 62px;
  margin: 5px;
}

.frames__add-frame:hover, .frames__revers-frame:hover {
  border-color: #ffd700;
}
